/**
 * Comprehensive error handling system for streaming application
 */

export enum ErrorCategory {
	NETWORK = 'NETWORK',
	STREAM = 'STREAM',
	PLAYER = 'PLAYER',
	ADDON = 'ADDON',
	PERMISSION = 'PERMISSION',
	VALIDATION = 'VALIDATION',
	UNKNOWN = 'UNKNOWN'
}

export enum ErrorSeverity {
	LOW = 'LOW',
	MEDIUM = 'MEDIUM',
	HIGH = 'HIGH',
	CRITICAL = 'CRITICAL'
}

export interface ErrorContext {
	timestamp: number;
	userAgent?: string;
	url?: string;
	userId?: string;
	sessionId?: string;
	metadata?: Record<string, unknown>;
}

export interface StreamingErrorOptions {
	category: ErrorCategory;
	severity: ErrorSeverity;
	message: string;
	originalError?: Error;
	context?: Partial<ErrorContext>;
	recoverable: boolean;
	userMessage?: string;
	actionable?: boolean;
	retryable?: boolean;
}

export class StreamingError extends Error {
	public readonly category: ErrorCategory;
	public readonly severity: ErrorSeverity;
	public readonly originalError?: Error;
	public readonly context: ErrorContext;
	public readonly recoverable: boolean;
	public readonly userMessage: string;
	public readonly actionable: boolean;
	public readonly retryable: boolean;
	public readonly errorId: string;

	constructor(options: StreamingErrorOptions) {
		super(options.message);
		this.name = 'StreamingError';

		this.category = options.category;
		this.severity = options.severity;
		this.originalError = options.originalError;
		this.recoverable = options.recoverable;
		this.actionable = options.actionable ?? true;
		this.retryable = options.retryable ?? false;

		this.context = {
			timestamp: Date.now(),
			userAgent: typeof navigator !== 'undefined' ? navigator.userAgent : undefined,
			url: typeof window !== 'undefined' ? window.location.href : undefined,
			...options.context
		};

		this.userMessage = options.userMessage || this.getDefaultUserMessage();
		this.errorId = this.generateErrorId();

		// Maintain stack trace
		if (Error.captureStackTrace) {
			Error.captureStackTrace(this, StreamingError);
		}
	}

	private getDefaultUserMessage(): string {
		switch (this.category) {
			case ErrorCategory.NETWORK:
				return 'Network connection issues detected. Please check your internet connection and try again.';
			case ErrorCategory.STREAM:
				return 'Unable to load the video stream. The content may be temporarily unavailable.';
			case ErrorCategory.PLAYER:
				return 'Video player encountered an error. Try refreshing the page or using a different browser.';
			case ErrorCategory.ADDON:
				return 'Content source is currently unavailable. Please try again later.';
			case ErrorCategory.PERMISSION:
				return 'Access denied. You may not have permission to view this content.';
			case ErrorCategory.VALIDATION:
				return 'Invalid request. Please check your input and try again.';
			default:
				return 'An unexpected error occurred. Please try again.';
		}
	}

	private generateErrorId(): string {
		return `${this.category}_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
	}

	public toJSON() {
		return {
			errorId: this.errorId,
			name: this.name,
			message: this.message,
			userMessage: this.userMessage,
			category: this.category,
			severity: this.severity,
			recoverable: this.recoverable,
			actionable: this.actionable,
			retryable: this.retryable,
			context: this.context,
			stack: this.stack,
			originalError: this.originalError
				? {
						name: this.originalError.name,
						message: this.originalError.message,
						stack: this.originalError.stack
					}
				: undefined
		};
	}

	public static createNetworkError(
		message: string,
		originalError?: Error,
		context?: Partial<ErrorContext>
	): StreamingError {
		return new StreamingError({
			category: ErrorCategory.NETWORK,
			severity: ErrorSeverity.HIGH,
			message,
			originalError,
			context,
			recoverable: true,
			retryable: true,
			userMessage: 'Network connection failed. Please check your internet connection and try again.'
		});
	}

	public static createStreamError(
		message: string,
		originalError?: Error,
		context?: Partial<ErrorContext>
	): StreamingError {
		return new StreamingError({
			category: ErrorCategory.STREAM,
			severity: ErrorSeverity.HIGH,
			message,
			originalError,
			context,
			recoverable: true,
			retryable: true,
			userMessage: 'Failed to load video stream. The content may be temporarily unavailable.'
		});
	}

	public static createPlayerError(
		message: string,
		originalError?: Error,
		context?: Partial<ErrorContext>
	): StreamingError {
		return new StreamingError({
			category: ErrorCategory.PLAYER,
			severity: ErrorSeverity.MEDIUM,
			message,
			originalError,
			context,
			recoverable: true,
			retryable: false,
			userMessage: 'Video player error. Try refreshing the page or using a different browser.'
		});
	}

	public static createAddonError(
		message: string,
		originalError?: Error,
		context?: Partial<ErrorContext>
	): StreamingError {
		return new StreamingError({
			category: ErrorCategory.ADDON,
			severity: ErrorSeverity.MEDIUM,
			message,
			originalError,
			context,
			recoverable: true,
			retryable: true,
			userMessage: 'Content source temporarily unavailable. Please try again later.'
		});
	}

	public static createValidationError(
		message: string,
		context?: Partial<ErrorContext>
	): StreamingError {
		return new StreamingError({
			category: ErrorCategory.VALIDATION,
			severity: ErrorSeverity.LOW,
			message,
			context,
			recoverable: false,
			retryable: false,
			userMessage: 'Invalid request. Please check your input and try again.'
		});
	}

	public static createPermissionError(
		message: string,
		context?: Partial<ErrorContext>
	): StreamingError {
		return new StreamingError({
			category: ErrorCategory.PERMISSION,
			severity: ErrorSeverity.HIGH,
			message,
			context,
			recoverable: false,
			retryable: false,
			userMessage: 'Access denied. You may not have permission to view this content.'
		});
	}
}

export type ErrorHandler = (error: StreamingError) => void;
export type ErrorRecoveryHandler = (error: StreamingError) => Promise<boolean>;
