/**
 * Advanced caching system for API responses and data
 */

import type { CacheEntry, CacheOptions } from '$lib/types/api';
import { logger } from './logger';

export class Cache<T = unknown> {
	private cache = new Map<string, CacheEntry<T>>();
	private options: Required<CacheOptions>;
	private cleanupTimer: ReturnType<typeof setInterval> | null = null;

	constructor(options: CacheOptions = {}) {
		this.options = {
			ttl: 5 * 60 * 1000, // 5 minutes default
			maxSize: 50 * 1024 * 1024, // 50MB default
			maxEntries: 1000,
			onEvict: () => {},
			...options
		};

		// Start cleanup timer
		this.startCleanupTimer();

		logger.debug(
			'Cache initialized',
			{
				ttl: this.options.ttl,
				maxSize: this.options.maxSize,
				maxEntries: this.options.maxEntries
			},
			'CACHE'
		);
	}

	private startCleanupTimer(): void {
		// Clean up expired entries every 5 minutes
		this.cleanupTimer = setInterval(
			() => {
				this.cleanup();
			},
			5 * 60 * 1000
		);
	}

	private cleanup(): void {
		const now = Date.now();
		let removedCount = 0;

		for (const [key, entry] of this.cache.entries()) {
			if (this.isExpired(entry, now)) {
				this.options.onEvict(key, entry.data);
				this.cache.delete(key);
				removedCount++;
			}
		}

		if (removedCount > 0) {
			logger.debug(`Cleaned up ${removedCount} expired cache entries`, undefined, 'CACHE');
		}
	}

	private isExpired(entry: CacheEntry<T>, now = Date.now()): boolean {
		return now > entry.timestamp + entry.ttl;
	}

	private enforceLimit(): void {
		// Remove oldest entries if we exceed max entries
		while (this.cache.size > this.options.maxEntries) {
			const oldestKey = this.cache.keys().next().value;
			if (oldestKey) {
				const entry = this.cache.get(oldestKey);
				if (entry) {
					this.options.onEvict(oldestKey, entry.data);
				}
				this.cache.delete(oldestKey);
			}
		}

		// Check total size if size tracking is enabled
		if (this.getTotalSize() > this.options.maxSize) {
			this.evictLRU();
		}
	}

	private getTotalSize(): number {
		let totalSize = 0;
		for (const entry of this.cache.values()) {
			totalSize += entry.size || 0;
		}
		return totalSize;
	}

	private evictLRU(): void {
		// Simple LRU: remove oldest entries until under size limit
		const entries = Array.from(this.cache.entries()).sort(
			(a, b) => a[1].timestamp - b[1].timestamp
		);

		for (const [key, entry] of entries) {
			this.options.onEvict(key, entry.data);
			this.cache.delete(key);

			if (this.getTotalSize() <= this.options.maxSize) {
				break;
			}
		}
	}

	private calculateSize(data: T): number {
		try {
			return JSON.stringify(data).length * 2; // Rough estimate (UTF-16)
		} catch {
			return 1024; // Default 1KB for non-serializable objects
		}
	}

	public set(key: string, data: T, customTtl?: number): void {
		const ttl = customTtl || this.options.ttl;
		const size = this.calculateSize(data);

		const entry: CacheEntry<T> = {
			key,
			data,
			timestamp: Date.now(),
			ttl,
			size
		};

		// Remove existing entry if it exists
		if (this.cache.has(key)) {
			const oldEntry = this.cache.get(key);
			if (oldEntry) {
				this.options.onEvict(key, oldEntry.data);
			}
		}

		this.cache.set(key, entry);
		this.enforceLimit();

		logger.debug(
			`Cached data for key: ${key}`,
			{
				size,
				ttl,
				totalEntries: this.cache.size
			},
			'CACHE'
		);
	}

	public get(key: string): T | null {
		const entry = this.cache.get(key);

		if (!entry) {
			logger.debug(`Cache miss for key: ${key}`, undefined, 'CACHE');
			return null;
		}

		if (this.isExpired(entry)) {
			this.options.onEvict(key, entry.data);
			this.cache.delete(key);
			logger.debug(`Cache expired for key: ${key}`, undefined, 'CACHE');
			return null;
		}

		// Update timestamp for LRU
		entry.timestamp = Date.now();

		logger.debug(`Cache hit for key: ${key}`, undefined, 'CACHE');
		return entry.data;
	}

	public has(key: string): boolean {
		const entry = this.cache.get(key);
		if (!entry) return false;

		if (this.isExpired(entry)) {
			this.options.onEvict(key, entry.data);
			this.cache.delete(key);
			return false;
		}

		return true;
	}

	public delete(key: string): boolean {
		const entry = this.cache.get(key);
		if (entry) {
			this.options.onEvict(key, entry.data);
		}
		return this.cache.delete(key);
	}

	public clear(): void {
		for (const [key, entry] of this.cache.entries()) {
			this.options.onEvict(key, entry.data);
		}
		this.cache.clear();
		logger.info('Cache cleared', undefined, 'CACHE');
	}

	public getStats() {
		const now = Date.now();
		let expiredCount = 0;
		let totalSize = 0;

		for (const entry of this.cache.values()) {
			if (this.isExpired(entry, now)) {
				expiredCount++;
			}
			totalSize += entry.size || 0;
		}

		return {
			totalEntries: this.cache.size,
			expiredEntries: expiredCount,
			activeEntries: this.cache.size - expiredCount,
			totalSize,
			maxSize: this.options.maxSize,
			maxEntries: this.options.maxEntries,
			utilizationPercent: (totalSize / this.options.maxSize) * 100
		};
	}

	public destroy(): void {
		if (this.cleanupTimer) {
			clearInterval(this.cleanupTimer);
			this.cleanupTimer = null;
		}
		this.clear();
		logger.debug('Cache destroyed', undefined, 'CACHE');
	}
}

// Cache implementations for different data types
export class RequestCache extends Cache<unknown> {
	constructor() {
		super({
			ttl: 5 * 60 * 1000, // 5 minutes for API requests
			maxEntries: 500,
			maxSize: 25 * 1024 * 1024 // 25MB
		});
	}

	public cacheRequest(
		url: string,
		params: Record<string, unknown>,
		response: unknown,
		ttl?: number
	): void {
		const key = this.createRequestKey(url, params);
		this.set(key, response, ttl);
	}

	public getRequest(url: string, params: Record<string, unknown>): unknown | null {
		const key = this.createRequestKey(url, params);
		return this.get(key);
	}

	private createRequestKey(url: string, params: Record<string, unknown>): string {
		const sortedParams = Object.keys(params)
			.sort()
			.reduce(
				(result, key) => {
					result[key] = params[key];
					return result;
				},
				{} as Record<string, unknown>
			);

		return `${url}:${JSON.stringify(sortedParams)}`;
	}
}

export class MetadataCache extends Cache<unknown> {
	constructor() {
		super({
			ttl: 30 * 60 * 1000, // 30 minutes for metadata
			maxEntries: 1000,
			maxSize: 50 * 1024 * 1024 // 50MB
		});
	}
}

export class StreamCache extends Cache<unknown> {
	constructor() {
		super({
			ttl: 10 * 60 * 1000, // 10 minutes for stream data
			maxEntries: 200,
			maxSize: 10 * 1024 * 1024 // 10MB
		});
	}
}

// Global cache instances
export const requestCache = new RequestCache();
export const metadataCache = new MetadataCache();
export const streamCache = new StreamCache();

// Utility functions
export function createCacheKey(...parts: (string | number | boolean)[]): string {
	return parts.map((part) => String(part)).join(':');
}

export async function withCache<T>(
	cache: Cache<T>,
	key: string,
	fetcher: () => Promise<T>,
	ttl?: number
): Promise<T> {
	// Try to get from cache first
	const cached = cache.get(key);
	if (cached !== null) {
		return cached;
	}

	// Fetch and cache
	logger.debug(`Fetching data for cache key: ${key}`, undefined, 'CACHE');
	const data = await fetcher();
	cache.set(key, data, ttl);
	return data;
}

// Clean up caches on page unload
if (typeof window !== 'undefined') {
	window.addEventListener('beforeunload', () => {
		requestCache.destroy();
		metadataCache.destroy();
		streamCache.destroy();
	});
}
