/**
 * Enhanced logging system for production-ready streaming application
 */

import type { LogEntry } from '$lib/types/api';
import { browser } from '$app/environment';

export enum LogLevel {
	DEBUG = 0,
	INFO = 1,
	WARN = 2,
	ERROR = 3
}

export interface LoggerOptions {
	level: LogLevel;
	enableConsole: boolean;
	enableRemote: boolean;
	remoteEndpoint?: string;
	bufferSize: number;
	flushInterval: number;
	sessionId?: string;
	userId?: string;
}

export class Logger {
	private options: LoggerOptions;
	private buffer: LogEntry[] = [];
	private flushTimer: ReturnType<typeof setInterval> | null = null;
	private sessionId: string;

	constructor(options: Partial<LoggerOptions> = {}) {
		this.options = {
			level: LogLevel.INFO,
			enableConsole: true,
			enableRemote: false,
			bufferSize: 100,
			flushInterval: 30000, // 30 seconds
			...options
		};

		this.sessionId = options.sessionId || this.generateSessionId();

		if (this.options.enableRemote && browser) {
			this.startFlushTimer();
		}

		// Capture unhandled errors
		if (browser) {
			window.addEventListener('error', (event) => {
				this.error('Unhandled error', {
					message: event.message,
					filename: event.filename,
					lineno: event.lineno,
					colno: event.colno,
					error: event.error?.stack
				});
			});

			window.addEventListener('unhandledrejection', (event) => {
				this.error('Unhandled promise rejection', {
					reason: event.reason,
					promise: event.promise
				});
			});
		}
	}

	private generateSessionId(): string {
		return `session_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
	}

	private shouldLog(level: LogLevel): boolean {
		return level >= this.options.level;
	}

	private createLogEntry(
		level: 'debug' | 'info' | 'warn' | 'error',
		message: string,
		category: string,
		metadata?: Record<string, unknown>
	): LogEntry {
		return {
			level,
			message,
			timestamp: Date.now(),
			category,
			metadata,
			userId: this.options.userId,
			sessionId: this.sessionId
		};
	}

	private addToBuffer(entry: LogEntry): void {
		this.buffer.push(entry);

		if (this.buffer.length >= this.options.bufferSize) {
			this.flush();
		}
	}

	private async flush(): Promise<void> {
		if (this.buffer.length === 0 || !this.options.enableRemote || !this.options.remoteEndpoint) {
			return;
		}

		const logsToSend = [...this.buffer];
		this.buffer = [];

		try {
			await fetch(this.options.remoteEndpoint, {
				method: 'POST',
				headers: {
					'Content-Type': 'application/json'
				},
				body: JSON.stringify({ logs: logsToSend })
			});
		} catch (error) {
			// Silently fail remote logging to avoid infinite loops
			if (this.options.enableConsole) {
				console.warn('Failed to send logs to remote endpoint:', error);
			}
		}
	}

	private startFlushTimer(): void {
		this.flushTimer = setInterval(() => {
			this.flush();
		}, this.options.flushInterval);
	}

	private log(
		level: LogLevel,
		levelName: 'debug' | 'info' | 'warn' | 'error',
		message: string,
		category: string,
		metadata?: Record<string, unknown>
	): void {
		if (!this.shouldLog(level)) {
			return;
		}

		const entry = this.createLogEntry(levelName, message, category, metadata);

		// Console logging
		if (this.options.enableConsole && browser) {
			const consoleMethod = console[levelName] || console.log;
			const timestamp = new Date(entry.timestamp).toISOString();
			const prefix = `[${timestamp}] [${levelName.toUpperCase()}] [${category}]`;

			if (metadata && Object.keys(metadata).length > 0) {
				consoleMethod(`${prefix} ${message}`, metadata);
			} else {
				consoleMethod(`${prefix} ${message}`);
			}
		}

		// Remote logging
		if (this.options.enableRemote) {
			this.addToBuffer(entry);
		}
	}

	public debug(message: string, metadata?: Record<string, unknown>, category = 'APP'): void {
		this.log(LogLevel.DEBUG, 'debug', message, category, metadata);
	}

	public info(message: string, metadata?: Record<string, unknown>, category = 'APP'): void {
		this.log(LogLevel.INFO, 'info', message, category, metadata);
	}

	public warn(message: string, metadata?: Record<string, unknown>, category = 'APP'): void {
		this.log(LogLevel.WARN, 'warn', message, category, metadata);
	}

	public error(message: string, metadata?: Record<string, unknown>, category = 'APP'): void {
		this.log(LogLevel.ERROR, 'error', message, category, metadata);
	}

	// Category-specific logging methods
	public player(
		level: 'debug' | 'info' | 'warn' | 'error',
		message: string,
		metadata?: Record<string, unknown>
	): void {
		this[level](message, metadata, 'PLAYER');
	}

	public stream(
		level: 'debug' | 'info' | 'warn' | 'error',
		message: string,
		metadata?: Record<string, unknown>
	): void {
		this[level](message, metadata, 'STREAM');
	}

	public addon(
		level: 'debug' | 'info' | 'warn' | 'error',
		message: string,
		metadata?: Record<string, unknown>
	): void {
		this[level](message, metadata, 'ADDON');
	}

	public network(
		level: 'debug' | 'info' | 'warn' | 'error',
		message: string,
		metadata?: Record<string, unknown>
	): void {
		this[level](message, metadata, 'NETWORK');
	}

	public ui(
		level: 'debug' | 'info' | 'warn' | 'error',
		message: string,
		metadata?: Record<string, unknown>
	): void {
		this[level](message, metadata, 'UI');
	}

	public performance(
		level: 'debug' | 'info' | 'warn' | 'error',
		message: string,
		metadata?: Record<string, unknown>
	): void {
		this[level](message, metadata, 'PERFORMANCE');
	}

	// Utility methods
	public setLevel(level: LogLevel): void {
		this.options.level = level;
	}

	public setUserId(userId: string): void {
		this.options.userId = userId;
	}

	public getSessionId(): string {
		return this.sessionId;
	}

	public async forceFlush(): Promise<void> {
		await this.flush();
	}

	public destroy(): void {
		if (this.flushTimer) {
			clearInterval(this.flushTimer);
			this.flushTimer = null;
		}
		this.flush(); // Final flush
	}

	// Performance timing helpers
	public time(label: string): void {
		if (this.shouldLog(LogLevel.DEBUG)) {
			if (browser && console.time) {
				console.time(label);
			}
		}
	}

	public timeEnd(label: string, metadata?: Record<string, unknown>): void {
		if (this.shouldLog(LogLevel.DEBUG)) {
			if (browser && console.timeEnd) {
				console.timeEnd(label);
			}
			this.debug(`Timer '${label}' finished`, metadata, 'PERFORMANCE');
		}
	}

	// Group logging for related operations
	public group(label: string): void {
		if (this.options.enableConsole && browser && console.group) {
			console.group(label);
		}
		this.debug(`Started group: ${label}`, undefined, 'GROUP');
	}

	public groupEnd(label?: string): void {
		if (this.options.enableConsole && browser && console.groupEnd) {
			console.groupEnd();
		}
		if (label) {
			this.debug(`Ended group: ${label}`, undefined, 'GROUP');
		}
	}
}

// Create default logger instance
const isDevelopment = import.meta.env?.DEV || false;

export const logger = new Logger({
	level: isDevelopment ? LogLevel.DEBUG : LogLevel.INFO,
	enableConsole: true,
	enableRemote: false, // Enable when you have a logging endpoint
	bufferSize: 50,
	flushInterval: 30000
});

// Convenience exports for direct use
export const debug = (message: string, metadata?: Record<string, unknown>, category?: string) =>
	logger.debug(message, metadata, category);
export const info = (message: string, metadata?: Record<string, unknown>, category?: string) =>
	logger.info(message, metadata, category);
export const warn = (message: string, metadata?: Record<string, unknown>, category?: string) =>
	logger.warn(message, metadata, category);
export const error = (message: string, metadata?: Record<string, unknown>, category?: string) =>
	logger.error(message, metadata, category);

// Performance helpers
export const time = (label: string) => logger.time(label);
export const timeEnd = (label: string, metadata?: Record<string, unknown>) =>
	logger.timeEnd(label, metadata);

// Clean up on page unload
if (browser) {
	window.addEventListener('beforeunload', () => {
		logger.destroy();
	});
}
